a:hover{
    text-decoration: none;
}

ul{
    padding: 0;
}

.header{
    font-weight: bold;
    font-size: 1.5rem;
    text-align: center;
}

.headers{
    margin-top: 10em;
}


.content-header{
    display: flex;
    justify-content: center;
    border-bottom: .2px solid #ccc;
}


.list{
    list-style-type: none;
    display: flex;
    position: relative;
    margin: 0;
    top: 0.05rem;
}

.link{
  text-decoration: none;
  color: #000;
  border-bottom: 0.2px solid #ccc;
  padding: 1em;
  font-weight: bold;
  border: 0.2px solid #ccc;
  cursor: pointer;
  background-color: rgb(224, 224, 224);
}

.link:hover{
    background-color: rgba(204, 204, 204, 0.52);
}

.link-selected {
    border-bottom: 0.2px solid transparent;
    background-color: white;
}

.link-selected:hover{
    background-color: white;
}