@use '../../../globals.scss';
/* Clear floats after the columns */
.row:after {
    content: "";
    display: table;
    clear: both;
}


.container {
    border-radius: 5px;
    padding: 2em;
}


.inputs {
    @include globals.inputs;
}

.labels {
    padding: 12px 12px 12px 0;
    display: inline-block;
}

.col-25 {
    float: left;
    width: 25%;
    margin-top: 6px;
    border: .2px solid #ccc;
    border-right: .2px solid transparent ;
    padding-left: 2rem;
}


.col-75 {
    float: left;
    width: 75%;
    margin-top: 6px;
}


#help{
    height: 65px;
}

.chars{
  color: green;
  margin: 0 auto;
  width: 350px;
}

.check-container{
    display: flex;
    width: 100%;
    margin-top: 2rem;
}

.orange-link{
  color: globals.$orange;
  text-decoration: none;
}


.orange-link:hover{
    text-decoration: underline;
}
.btn-container{
    display: flex;
    justify-content: center;
    margin-top: 2rem;
}

.subBtn{
    @include globals.orangeBtn;
}

.frm{
    width: 650px;
    margin: 0 auto;
    margin-top: 3rem;
}
  .iti{
    width: 100%;
}


@media screen and (max-width: 750px) {
    .col-25, .col-75, input[type=submit],.frm {
        width: 100%;
        margin-top: 0;
    }
    .col-25{
        border: 0.2px solid transparent;;
    }
}